.approach {
  position: relative;
  padding-top: 150px;
  padding-bottom: 135px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__head {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 75px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
    @include response(767px) {
      display: block;
    }
  }
  &__head-left {
    position: relative;
    padding-right: 20px;
    @include response(767px) {
      padding-right: 0;
    }
  }
  &__title {
    margin-bottom: 18px;
    @include response(767px) {
      margin-bottom: 25px;
    }
  }
  &__description {
    position: relative;
    @include response(767px) {
      margin-bottom: 40px;
    }
  }
  &__head-right {
    position: relative;
    max-width: 560px;
    width: 100%;
  }
  &__head-text {
    position: relative;
  }
  &__list {
    position: relative;
    display: flex;
    justify-content: center;
    padding-left: 60px;
    padding-right: 60px;
    margin-bottom: 55px;
  }
  &__info {
    position: relative;
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
  }
  &__info-icon {
    flex: 0 0 36px;
    max-width: 36px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 2px solid #c5c1ff;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background: rgba(#c5c1ff, 0.3);
      top: 50%;
      left: 50%;
      margin-top: -10px;
      margin-left: -10px;
    }
    svg {
      fill: #c5c1ff;
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(16.666% - 40px);
    max-width: calc(16.666% - 40px);
    margin-left: 20px;
    margin-right: 20px;
    @include desktop-hover() {
      .approach__item-count {
        opacity: 0;
      }
      .approach__item-title span {
        background: #c5c1ff;
      }
      .approach__item-img {
        &::before {
          opacity: 1;
          transform: scaleY(1);
        }
      }
    }
  }
  &__item-count {
    position: relative;
    margin-bottom: 7px;
    font-family: "Raleway";
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: #c5c1ff;
    transition: 0.3s ease-in-out;
  }
  &__item-img {
    position: relative;
    height: 360px;
    background: #f9f9ff;
    margin-bottom: 45px;
    &::before {
      content: "";
      position: absolute;
      top: -25px;
      bottom: -25px;
      left: 0;
      right: 0;
      background: $blue;
      opacity: 0;
      transform: scaleY(0.9);
      transition: 0.3s ease-in-out;
    }
  }
  &__item-title {
    text-align: center;
    span {
      display: inline-block;
      vertical-align: top;
      font-weight: 500;
      font-size: 18px;
      line-height: 27px;
      color: #011a28;
      padding: 5px 20px;
      transition: 0.3s ease-in-out;
    }
  }
  &__text {
    max-width: 560px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
}
