.first-screen {
  position: relative;
  background: #f9f9ff;
  &__box {
    position: relative;
    height: 100vh;
    display: flex;
    padding: 130px 30px 75px;
    max-width: 1506px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    @include response(1200px) {
      display: block;
      padding-top: 90px;
      padding-bottom: 30px;
      height: auto;
    }
  }
  &__info {
    position: relative;
    max-width: 438px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__info-box {
    position: relative;
    padding-top: 150px;
    @include response(1439px) {
      padding-top: 100px;
    }
    @include response(1200px) {
      padding-top: 0;
    }
  }
  &__title {
    &.title {
      margin-bottom: 50px;
      @include response(1200px) {
        margin-bottom: 20px;
      }
    }
  }
  &__text {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 0;
    }
  }
  &__row {
    display: flex;
    @include response(1200px) {
      display: none;
    }
  }
  &__cell {
    position: relative;
    margin-right: 80px;
  }
  &__cell-btn {
    margin-bottom: 6px;
  }
  &__cell-text {
    font-size: 13px;
    line-height: 175%;
    color: #7d7d8c;
  }
  &__animation {
    position: relative;
    flex: 1 1 auto;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @include response(1200px) {
      margin-left: -25px;
      margin-right: -25px;
      height: auto;
    }
    img {
      max-height: 100%;
      width: auto;
    }
  }
}
