.table-scroll {
  position: relative;
  margin: 0 0 40px;
  @include response(1200px) {
    margin: 0 0 30px;
  }
  @include response(767px) {
    margin-left: -30px;
    margin-right: -30px;
    padding: 1px 30px;
    overflow-x: auto;
    display: flex;
    &::after {
      content: "";
      display: inline-block;
      flex: 0 0 1px;
      height: 1px;
      margin-left: 1px;
    }
    table {
      flex: 0 0 767px;
      width: 767px !important;
    }
  }
}
