.form {
  position: relative;
  &__title {
    font-family: "Raleway";
    font-style: normal;
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    color: #011a28;
    margin-bottom: 14px;
  }
  &__text {
    position: relative;
    max-width: 600px;
    font-size: 15px;
    line-height: 175%;
    color: #7d7d8c;
    margin-bottom: 75px;
    @include response(1200px) {
      margin-bottom: 40px;
    }
    &--small {
      max-width: 300px;
    }
  }
  &__box {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    @include response(767px) {
      display: block;
    }
  }
  &__box-left {
    position: relative;
    flex: 0 0 340px;
    max-width: 340px;
    width: 100%;
    @include response(767px) {
      max-width: 100%;
      margin-bottom: 25px;
    }
  }
  &__row {
    margin-bottom: 25px;
    position: relative;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__input-box {
    position: relative;
    &--textarea {
      display: flex;
      flex-direction: column;
      width: 100%;
    }
  }
  &__input-label {
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    line-height: 175%;
    color: #7d7d8c;
  }
  &__input {
    width: 100%;
    border: 0;
    border-bottom: 1px solid #cacad7;
    height: 50px;
    transition: 0.3s ease-in-out;
    color: $black;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    background: transparent;
    padding: 0;
    border-radius: 0;
    &:focus {
      border-bottom-color: $blue;
    }
  }
  &__textarea {
    resize: none;
    width: 100%;
    border: 0;
    border-bottom: 1px solid #cacad7;
    transition: 0.3s ease-in-out;
    color: $black;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    background: transparent;
    padding: 14px 0;
    flex: 1 1 auto;
    border-radius: 0;
    &:focus {
      border-bottom-color: $blue;
    }
  }
  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__captcha {
    position: relative;
    flex: 0 0 auto;
    margin-right: 30px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }
  &__btn {
    flex: 0 0 auto;
  }
  &__box-right {
    position: relative;
    flex: 0 0 340px;
    max-width: 340px;
    width: 100%;
    display: flex;
    @include response(767px) {
      max-width: 100%;
    }
  }
  &__count {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    font-size: 12px;
    line-height: 175%;
    color: #7d7d8c;
  }
  &__count-current {
    flex: 0 0 auto;
  }
  &__count-maximum {
    flex: 0 0 auto;
  }
}
