.studies {
  position: relative;
  padding-bottom: 150px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__list {
    position: relative;
    margin-top: -200px;
    margin-bottom: 50px;
    @include response(1200px) {
      margin-top: 0;
    }
    &--margin-top-0 {
      margin-top: 0;
    }
  }
  &__item {
    position: relative;
    margin-bottom: 100px;
    display: flex;
    background: #fff;
    border: 1px solid #cacad7;
    border-radius: 50px;
    overflow: hidden;
    &--full {
      box-shadow: 0px 5px 75px rgba(1, 26, 40, 0.06);
      border-color: #fff;
      border-width: 3px;
      margin-left: -172px;
      margin-right: -172px;
      @include response(1520px) {
        margin-right: 0;
        margin-left: 0;
      }
    }
    &--small {
      justify-content: space-between;
      margin-bottom: 75px;
    }
    @include response(1200px) {
      display: block;
      margin-bottom: 35px;
    }
    @include response(767px) {
      margin-right: -20px;
      margin-left: -20px;
    }
  }
  &__item-left {
    position: relative;
    padding: 100px 70px 100px 100px;
    .studies__item--small & {
      padding: 75px;
      @include response(1200px) {
        padding: 50px 30px 30px;
      }
    }
    @include response(1200px) {
      padding: 50px 30px 30px;
    }
  }
  &__item-title {
    margin-bottom: 31px;
  }
  &__item-labels {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-bottom: 25px;
  }
  &__item-label {
    flex: 0 0 auto;
    position: relative;
    background: #f9f9ff;
    border-radius: 5px;
    padding: 5px 15px;
    font-weight: 500;
    font-size: 13px;
    line-height: 23px;
    color: #011a28;
    margin-left: 10px;
    margin-bottom: 10px;
  }
  &__item-text {
    margin-bottom: 50px;
    .studies__item--small & {
      max-width: 355px;
      margin-bottom: 40px;
    }
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__item-btn-box {
    position: relative;
  }
  &__item-btn {
    position: relative;
  }
  &__item-right {
    position: relative;
    flex: 0 0 560px;
    max-width: 560px;
    width: 100%;
    .studies__item--full & {
      flex: 0 0 900px;
      max-width: 900px;
      @include response(1520px) {
        flex: 0 0 560px;
        max-width: 560px;
      }
      @include response(1200px) {
        max-width: 100%;
      }
    }
    @include response(1200px) {
      max-width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__load-more {
    position: relative;
    text-align: center;
  }
  &__load-more-btn {
    @include desktop-hover() {
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__item-search {
    position: relative;
  }
  &__item-search-title {
    font-family: "Raleway";
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: #011a28;
    margin-bottom: 23px;
  }
  &__item-search-row {
    position: relative;
    display: flex;
    align-items: center;
    background: #f9f9ff;
    border-radius: 99px;
    padding: 13px 15px;
    max-width: 300px;
  }
  &__item-search-icon {
    flex: 0 0 auto;
    margin-right: 15px;
  }
  &__item-search-name {
    font-size: 15px;
    line-height: 24px;
    color: #011a28;
    flex: 1 1 auto;
    padding-right: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  &__item-search-value {
    flex: 0 0 auto;
    font-family: "Raleway";
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;
    color: #7d7d8c;
    padding: 5px;
    border-radius: 5px;
    background: #fff;
  }
}
