.blog-post {
  position: relative;
  &__top {
    position: relative;
    background: #f9f9ff;
    padding-top: 164px;
    overflow: hidden;
    @include response(1439px) {
      padding-top: 130px;
    }
    @include response(1200px) {
      padding-top: 90px;
      padding-bottom: 60px;
    }
  }
  &__top-box {
    position: relative;
    display: flex;
    align-items: center;
    @include response(1200px) {
      flex-direction: column-reverse;
    }
  }
  &__info {
    position: relative;
    flex: 0 0 430px;
    max-width: 430px;
    width: 100%;
    margin-right: 170px;
    @include response(1200px) {
      max-width: 100%;
      flex: 0 0 auto;
      margin-right: 0;
    }
  }
  &__title {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__top-row {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__date {
    position: relative;
    flex: 0 0 auto;
    margin-right: 40px;
    display: flex;
    align-items: center;
  }
  &__date-icon {
    flex: 0 0 auto;
    margin-right: 10px;
    svg {
      fill: #7d7d8c;
    }
  }
  &__date-val {
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    color: #7d7d8c;
  }
  &__by {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    overflow: auto;
  }
  &__by-title {
    flex: 0 0 auto;
    color: #7d7d8c;
    margin-right: 4px;
  }
  &__by-val {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #7d7d8c;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__top-img {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__body {
    position: relative;
    padding-top: 40px;
    padding-bottom: 100px;
    @include response(1200px) {
      padding-bottom: 50px;
    }
  }
  &__nav {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 70px;
    @include response(1200px) {
      margin-bottom: 50px;
    }
  }
  &__nav-btn {
    flex: 0 0 auto;
    margin-right: 40px;
    font-weight: 500;
    font-size: 13px;
    line-height: 23px;
    color: #fff;
    display: flex;
    align-items: center;
    border-radius: 5px;
    background: $blue;
    padding: 5px 15px;
    @include response(1200px) {
      display: none;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__nav-list {
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;
    flex: 0 0 1034px;
    max-width: 1034px;
    width: 100%;
    display: flex;
    align-items: center;
    @include response(1200px) {
      max-width: 100%;
      flex: 0 0 100%;
    }
    .slick-arrow {
      display: flex;
      align-items: center;
      position: absolute;
      cursor: pointer;
      padding: 10px 15px;
      top: 0;
      bottom: 0;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        svg {
          fill: $black;
        }
      }
      svg {
        fill: #7d7d8c;
        transition: 0.3s ease-in-out;
      }
      &.slick-prev {
        right: 100%;
        svg {
          transform: scaleX(-1);
        }
      }
      &.slick-next {
        left: 100%;
      }
      &.slick-disabled {
        opacity: 0;
        visibility: hidden;
      }
    }
    .slick-slide {
      margin-right: 25px;
    }
    a {
      display: block;
      text-decoration: none;
      font-weight: 500;
      font-size: 13px;
      line-height: 20px;
      color: #7d7d8c;
      padding: 6px 0;
      transition: 0.3s ease-in-out;
      position: relative;
      overflow: hidden;
      &.active {
        color: $black;
        &::before {
          background: #c5c1ff;
          transform: translateY(0);
        }
      }
      @include desktop-hover() {
        color: $black;
        &::before {
          background: #c5c1ff;
          transform: translateY(0);
        }
      }
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 4px;
        background: #cacad7;
        transform: translateY(3px);
        transition: 0.3s ease-in-out;
      }
    }
  }
  &__content-box {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    @include response(1200px) {
      display: block;
    }
  }
  &__content {
    position: relative;
    flex: 0 0 640px;
    max-width: 640px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 40px;
    }
  }
  &__share {
    position: sticky;
    top: 94px;
    margin-left: 82px;
    margin-right: -130px;
    @include response(1200px) {
      position: relative;
      top: auto;
      margin-right: 0;
      margin-left: 0;
    }
  }
  &__share-list {
    list-style: none;
    padding: 0;
    margin: 0;
    @include response(1200px) {
      display: flex;
    }
    li {
      position: relative;
      margin-bottom: 10px;
      @include response(1200px) {
        margin-right: 10px;
      }
    }
    button {
      display: flex;
      padding: 0;
      border: 0;
      border-radius: 50%;
      overflow: hidden;
      cursor: pointer;
      transition: 0.2s ease-in-out;
      @include desktop-hover() {
        box-shadow: 0 5px 10px 5px rgba(#000, 0.1);
        transform: translateY(-2px);
      }
    }
  }
}
