.what-next {
  position: relative;
  background: #f9f9ff;
  padding-top: 200px;
  padding-bottom: 200px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__title {
    margin-bottom: 75px;
    font-family: 'Raleway';
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
    color: #011a28;
    @include response(1200px) {
      margin-bottom: 40px;
    }
  }
}
