.error-page {
  position: relative;
  padding: 287px 30px 150px;
  text-align: center;
  background: #f9f9ff;
  @include response(1440px) {
    padding-top: 130px;
    padding-bottom: 50px;
  }
  @include response(1200px) {
    padding-top: 100px;
  }
  &__title {
    position: relative;
  }
  &__description {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__text {
    position: relative;
    max-width: 640px;
    width: 100%;
    margin: 0 auto 44px;
    font-size: 15px;
    line-height: 175%;
    color: #011a28;
  }
  &__img {
    margin-bottom: 16px;
  }
  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__home-btn {
    flex: 0 0 auto;
    margin-right: 25px;
    @include response(767px) {
      margin-right: 10px;
    }
  }
  &__contacts-btn {
    flex: 0 0 auto;
    margin-left: 25px;
    @include response(767px) {
      margin-left: 10px;
    }
  }
}
