.with-us {
  position: relative;
  background: #f9f9ff;
  padding-top: 97px;
  padding-bottom: 96px;
  text-align: center;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__title {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__text {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__btn {
    position: relative;
  }
}
