.team {
  position: relative;
  padding-top: 166px;
  padding-bottom: 110px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__title {
    &.title {
      margin-bottom: 150px;
      @include response(1200px) {
        margin-bottom: 20px;
        text-align: center;
      }
    }
  }
  &__list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -40px;
  }
  &__item {
    position: relative;
    flex: 0 0 calc(25% - 40px);
    max-width: calc(25% - 40px);
    width: 100%;
    margin-left: 40px;
    margin-bottom: 40px;
    background: #f9f9ff;
    height: 430px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 30px 30px 35px;
    @include response(1200px) {
      flex: 0 0 calc(50% - 40px);
      max-width: calc(50% - 40px);
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 40px);
      max-width: calc(100% - 40px);
      margin-bottom: 20px;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 180px;
    width: 180px;
    height: 180px;
    background: #fff;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__item-box {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__item-name {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    color: #011a28;
    position: relative;
    margin-bottom: 17px;
    overflow: hidden;
    max-height: 81px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
  &__item-position-holder {
    position: relative;
    min-height: 46px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  &__item-position {
    background: #fff;
    border: 1px solid #c5c1ff;
    border-radius: 5px;
    position: relative;
    display: flex;
    align-items: center;
    padding: 6px 14px;
  }
  &__item-position-name {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    color: #011a28;
    position: relative;
    overflow: hidden;
    max-height: 32px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  &__item-position-img {
    position: relative;
    flex: 0 0 auto;
    margin-right: 10px;
  }
  &__item-country {
    font-weight: 500;
    font-size: 13px;
    line-height: 175%;
    text-align: center;
    color: #011a28;
    margin-top: 10px;
  }
}
