.results {
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__title {
    margin-bottom: 75px;
    @include response(1200px) {
      margin-bottom: 40px;
    }
  }
  &__decor {
    position: absolute;
    right: -158px;
    top: -70px;
    @include response(1200px) {
      display: none;
    }
  }
  &__btn-box {
    position: relative;
    @include response(1200px) {
      text-align: center;
    }
  }
  &__btn {
    position: relative;
  }
}
