.form-main {
  position: relative;
  display: flex;
  align-items: center;
  background: #f9f9ff;
  @include response(1200px) {
    display: block;
    padding-bottom: 75px;
  }
  &__img {
    flex: 0 0 33.632%;
    max-width: 33.632%;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 50px;
    }
    picture {
      display: block;
    }
    img {
      width: 100%;
    }
  }
  &__box {
    position: relative;
    margin-left: 8.8vw;
    flex: 0 0 730px;
    max-width: 730px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-left: 0;
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}
