.article-item {
  position: relative;
  flex: 0 0 calc(33.333% - 130px);
  max-width: calc(33.333% - 130px);
  width: 100%;
  margin-left: 130px;
  @include response(1200px) {
    margin-left: 30px;
    flex: 0 0 calc(33.333% - 30px);
    max-width: calc(33.333% - 30px);
  }
  @include response(767px) {
    flex: 0 0 calc(100% - 30px);
    max-width: calc(100% - 30px);
    margin-bottom: 50px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__img {
    display: block;
    margin-bottom: 40px;
    overflow: hidden;
    @include response(767px) {
      margin-bottom: 27px;
    }
    @include desktop-hover() {
      img {
        transform: scale(1.05);
      }
    }
    img {
      transition: 0.3s ease-in-out;
      width: 100%;
    }
  }
  &__title {
    display: block;
    text-decoration: none;
    font-family: "Raleway";
    font-weight: 600;
    font-size: 21px;
    line-height: 150%;
    color: #011a28;
    margin-bottom: 25px;
    transition: 0.3s ease-in-out;
    height: 63px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    @include desktop-hover() {
      color: $blue;
    }
  }
}

.article-list {
  position: relative;
  display: flex;
  margin-left: -130px;
  @include response(1200px) {
    margin-left: -30px;
  }
  @include response(767px) {
    flex-wrap: wrap;
  }
}
