.reviews {
  position: relative;
  padding-top: 150px;
  padding-bottom: 162px;
  background: $black;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__title {
    &.title {
      margin-bottom: 99px;
      color: #fff;
      @include response(1200px) {
        margin-bottom: 50px;
      }
    }
  }
  &__list {
    position: relative;
    display: flex;
    margin-left: -130px;
    @include response(1200px) {
      margin-left: 0;
      display: block;
      padding-bottom: 60px;
      .slick-dots {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        justify-content: center;
        list-style: none;
        padding: 0;
        margin: 0;
        li {
          margin-left: 3px;
          margin-right: 3px;
          flex: 0 0 auto;
          &.slick-active {
            button {
              opacity: 1;
              width: 24px;
            }
          }
        }
        button {
          display: block;
          font-size: 0;
          border-radius: 9px;
          padding: 0;
          border: 0;
          background: #7d7d8c;
          opacity: 0.5;
          width: 12px;
          height: 12px;
          transition: 0.3s ease-in-out;
        }
      }
    }
  }
  &__item {
    position: relative;
    flex: 0 0 calc(33.333% - 130px);
    max-width: calc(33.333% - 130px);
    width: 100%;
    margin-left: 130px;
    display: flex;
    flex-direction: column;
    @include response(1200px) {
      max-width: 100%;
      margin-left: 0;
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__item-box {
    position: relative;
    flex: 1 1 auto;
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__item-img {
    position: relative;
    margin-bottom: 50px;
    width: 84px;
    height: 84px;
    border-radius: 50%;
    overflow: hidden;
  }
  &__item-text {
    &.text-content {
      color: #fff;
    }
  }
  &__item-bottom {
    position: relative;
    flex: 0 0 auto;
  }
  &__item-name {
    font-weight: 600;
    font-size: 15px;
    line-height: 175%;
    text-transform: uppercase;
    color: #fff;
  }
  &__item-position {
    font-size: 15px;
    line-height: 175%;
    color: #fff;
  }
}
