.blog-home {
  position: relative;
  padding-top: 200px;
  padding-bottom: 200px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__box {
    position: relative;
    display: flex;
    @include response(1200px) {
      display: block;
    }
  }
  &__left {
    position: relative;
    flex: 1 1 auto;
    padding-right: 30px;
    @include response(1200px) {
      padding-right: 0;
    }
  }
  &__title {
    margin-bottom: 37px;
    font-family: "Raleway";
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    color: #011a28;
    @include response(1200px) {
      margin-bottom: 25px;
    }
  }
  &__btn-box {
    position: relative;
    @include response(1200px) {
      margin-bottom: 25px;
    }
  }
  &__right {
    position: relative;
    flex: 0 0 730px;
    max-width: 730px;
    width: 100%;
  }
  &__list {
    position: relative;
  }
}
