.contacts {
  position: relative;
  background: #f9f9ff;
  padding-top: 287px;
  @include response(1200px) {
    padding-top: 120px;
  }
  &__box {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-bottom: 156px;
    @include response(1200px) {
      display: block;
      padding-bottom: 75px;
    }
  }
  &__left {
    position: relative;
    flex: 0 0 330px;
    max-width: 330px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 50px;
    }
  }
  &__title-box {
    position: relative;
    margin-left: -175px;
    @include response(1550px) {
      margin-left: 0;
    }
  }
  &__title {
    position: relative;
  }
  &__description {
    margin-bottom: 97px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__list {
    position: relative;
    @include response(1200px) {
      display: flex;
      flex-wrap: wrap;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    @include response(1200px) {
      flex: 0 0 50%;
      max-width: 50%;
    }
    @include response(767px) {
      flex: 0 0 100%;
      max-width: 100%;
    }
    &:last-child {
      margin-bottom: 0;
      @include response(1200px) {
        margin-bottom: 25px;
      }
    }
  }
  &__item-img {
    flex: 0 0 48px;
    max-width: 48px;
    width: 48px;
    height: 48px;
    margin-right: 15px;
    border: 1px solid #cacad7;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }
  &__item-box {
    position: relative;
  }
  &__item-row {
    position: relative;
  }
  &__item-link {
    display: block;
    text-decoration: none;
    font-size: 15px;
    line-height: 175%;
    color: #011a28;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $blue;
    }
  }
  &__item-row-title {
    font-weight: 500;
    font-size: 13px;
    color: #3800d8;
  }
  &__right {
    position: relative;
    flex: 0 0 730px;
    max-width: 730px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__cities {
    position: relative;
    padding-top: 112px;
    padding-bottom: 125px;
    border-top: 1px solid #cacad7;
    background: #f9f9ff;
    @include response(1200px) {
      padding-top: 75px;
      padding-bottom: 75px;
    }
  }
  &__cities-box {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include response(1200px) {
      display: block;
    }
  }
  &__cities-left {
    position: relative;
    flex: 0 0 330px;
    max-width: 330px;
    width: 100%;
    display: flex;
    align-items: center;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 40px;
    }
  }
  &__cities-img {
    position: relative;
    flex: 0 0 48px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: #fff;
    border: 1px solid #cacad7;
    margin-right: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__cities-title {
    font-family: "Raleway";
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    color: #011a28;
  }
  &__cities-right {
    position: relative;
    flex: 0 0 730px;
    max-width: 730px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__cities-row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -90px;
    @include response(1200px) {
      margin-left: -30px;
    }
  }
  &__cities-cell {
    position: relative;
    flex: 0 0 calc(33.333% - 90px);
    max-width: calc(33.333% - 90px);
    width: 100%;
    margin-left: 90px;
    @include response(1200px) {
      flex: 0 0 calc(33.333% - 30px);
      max-width: calc(33.333% - 30px);
      margin-left: 30px;
    }
    @include response(767px) {
      flex: 0 0 calc(100% - 30px);
      max-width: calc(100% - 30px);
      margin-bottom: 50px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__cities-name {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    @include response(767px) {
      margin-bottom: 15px;
    }
  }
  &__cities-address {
    font-style: normal;
    font-size: 15px;
    line-height: 175%;
  }
}
