.social {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  @include response(1200px) {
    flex-wrap: wrap;
  }
  &__item {
    position: relative;
    flex: 0 0 auto;
    margin-right: 40px;
    @include response(1200px) {
      margin-right: 24px;
      margin-bottom: 20px;
      flex: 0 0 calc(50% - 24px);
    }
    .social--menu & {
      @include response(1200px) {
        margin-right: 10px;
        margin-left: 10px;
        flex: 0 0 auto;
        &:last-child {
          margin-right: 10px;
        }
      }
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: 600;
    font-size: 12px;
    line-height: 175%;
    color: #7d7d8c;
    text-transform: uppercase;
    transition: 0.3s ease-in-out;
    .social--menu & {
      color: #c5c1ff;
      @include response(1200px) {
        font-size: 0;
      }
      span {
        background: #c5c1ff;
      }
      svg {
        fill: #000;
      }
      @include desktop-hover() {
        color: #fff;
        span {
          background: #fff;
        }
      }
    }
    @include desktop-hover() {
      color: #000;
      span {
        background: $blue;
      }
    }
    span {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 0 0 24px;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      background: #7d7d8c;
      margin-right: 10px;
      transition: all 0.3s ease-in-out;
    }
    svg {
      fill: #fff;
    }
  }
  &--menu {
    @include response(1200px) {
      justify-content: center;
      .social__link span {
        width: 32px;
        flex: 0 0 32px;
        height: 32px;
        margin-right: 0;
      }
    }
  }
}
