.title {
  position: relative;
  font-family: "Raleway";
  font-weight: 600;
  font-size: 54px;
  line-height: 63px;
  color: $black;
  margin-bottom: 25px;
  @include response(1200px) {
    font-size: 27px;
    line-height: 40px;
  }
}

.title-small {
  position: relative;
  font-family: "Raleway";
  font-weight: 600;
  font-size: 36px;
  line-height: 50px;
  color: #011a28;
  @include response(1200px) {
    font-size: 27px;
    line-height: 40px;
  }
}

.title-description {
  font-family: "Raleway";
  position: relative;
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  color: $blue;
}
