.about {
  position: relative;
  &__we {
    position: relative;
  }
  &__we-title {
    padding-bottom: 50px;
    margin-top: -100px;
    margin-bottom: 100px;
    @include response(1200px) {
      margin-top: 0;
      padding-top: 40px;
      margin-bottom: 20px;
      padding-bottom: 0;
    }
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 100%;
      width: 75px;
      height: 5px;
      background: $blue;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__we-box {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-bottom: 115px;
    @include response(1200px) {
      display: block;
      padding-bottom: 40px;
    }
  }
  &__we-left {
    position: relative;
    flex: 0 0 430px;
    max-width: 430px;
    width: 100%;
    font-size: 18px;
    line-height: 175%;
    color: #011a28;
    @include response(1200px) {
      font-size: 15px;
      max-width: 100%;
      margin-bottom: 20px;
    }
    p {
      margin: 0;
    }
  }
  &__we-right {
    position: relative;
    flex: 0 0 560px;
    max-width: 560px;
    width: 100%;
  }
  &__we-text {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
  }
  &__we-triggers {
    position: relative;
    display: flex;
    flex-wrap: wrap;
  }
  &__we-triggers-item {
    position: relative;
    flex: 0 0 auto;
    margin-right: 10px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    padding: 9px 15px;
    font-weight: 500;
    font-size: 13px;
    line-height: 24px;
    color: #011a28;
    background: #f9f9ff;
    border-radius: 5px;
  }
  &__we-triggers-img {
    position: relative;
    flex: 0 0 auto;
    margin-right: 10px;
    max-width: 24px;
  }
  &__do {
    position: relative;
    padding-top: 100px;
    padding-bottom: 62px;
    background: #f9f9ff;
    @include response(1200px) {
      padding-top: 75px;
    }
  }
  &__do-box {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    @include response(1200px) {
      display: block;
    }
  }
  &__do-img {
    flex: 0 0 auto;
    margin-right: 59px;
    order: 2;
    @include response(1200px) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
  &__do-info {
    position: relative;
    flex: 0 0 560px;
    max-width: 560px;
    width: 100%;
    order: 1;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__do-title {
    margin-bottom: 18px;
  }
  &__do-description {
    margin-bottom: 75px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__do-text {
    position: relative;
  }
}
