.services {
  position: relative;
  @include response(1200px) {
    padding-top: 38px;
  }
  &__list {
    position: relative;
  }
  &__item {
    position: relative;
    padding-top: 150px;
    padding-bottom: 150px;
    @include response(1200px) {
      padding-top: 38px;
      padding-bottom: 38px;
    }
    &--padding-40 {
      padding-top: 40px;
      padding-bottom: 40px;
      @include response(1200px) {
        padding-top: 38px;
        padding-bottom: 38px;
      }
    }
    &--black {
      background: $black;
      padding-top: 225px;
      padding-bottom: 225px;
      @include response(1200px) {
        padding-top: 75px;
        padding-bottom: 75px;
      }
    }
    &--circle-decor {
      z-index: 1;
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        right: -220px;
        margin-top: -720px;
        width: 1330px;
        height: 1330px;
        background: rgba(202, 202, 215, 0.1);
        border-radius: 50%;
        @include response(1200px) {
          display: none;
        }
      }
    }
    &--padding-bottom-84 {
      padding-bottom: 84px;
      @include response(1200px) {
        padding-bottom: 38px;
      }
    }
    &--bg {
      background: #f9f9ff;
    }
  }
  &__item-circle-decor {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    @include response(1200px) {
      background: #f9f9ff;
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: -220px;
      margin-top: -720px;
      width: 1330px;
      height: 1330px;
      background: #f9f9ff;
      border-radius: 50%;
      @include response(1200px) {
        display: none;
      }
    }
  }
  &__container {
    display: flex;
    align-items: center;
    .services__item:nth-child(even) & {
      flex-direction: row-reverse;
      @include response(1200px) {
        flex-direction: column-reverse;
      }
    }
    @include response(1200px) {
      flex-direction: column-reverse;
    }
  }
  &__item-info {
    position: relative;
    flex: 0 0 560px;
    max-width: 560px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      flex: 0 0 auto;
      z-index: 2;
    }
  }
  &__item-title {
    font-family: "Raleway";
    font-weight: 600;
    font-size: 36px;
    line-height: 42px;
    color: #011a28;
    margin-bottom: 50px;
    .services__item--black & {
      color: #fff;
    }
    @include response(1200px) {
      font-weight: 600;
      font-size: 27px;
      line-height: 40px;
      margin-bottom: 40px;
    }
  }
  &__item-text {
    margin-bottom: 40px;
    .services__item--black & {
      color: #fff;
    }
  }
  &__item-labels {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 40px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__item-label {
    position: relative;
    text-decoration: none;
    display: flex;
    align-items: center;
    background: #f9f9ff;
    border-radius: 5px;
    margin-right: 10px;
    margin-bottom: 10px;
    flex: 0 0 auto;
    padding: 5px 15px;
    font-weight: 500;
    font-size: 13px;
    line-height: 175%;
    color: #011a28;
    max-width: 100%;
    .services__item--black & {
      color: #fff;
      background: #0b2233;
    }
    .services__item--circle-decor & {
      background: #fff;
    }
    .services__item--bg & {
      background: #fff;
    }
    img {
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__item-controls {
    position: relative;
    display: flex;
    align-items: center;
    @include response(767px) {
      flex-direction: column-reverse;
      align-items: flex-start;
    }
  }
  &__item-btn {
    flex: 0 0 auto;
    margin-right: 40px;
  }
  &__item-price {
    position: relative;
    display: flex;
    align-items: center;
    @include response(767px) {
      margin-bottom: 10px;
    }
  }
  &__item-price-title {
    flex: 0 0 auto;
    margin-right: 10px;
    font-size: 15px;
    line-height: 175%;
    color: #011a28;
    .services__item--black & {
      color: #fff;
    }
  }
  &__item-price-value {
    font-weight: 600;
    font-size: 23px;
    line-height: 175%;
    color: #ec4b37;
  }
  &__item-img {
    position: relative;
    flex: 0 0 auto;
    margin-left: 40px;
    z-index: 1;
    .services__item:nth-child(even) & {
      margin-left: 0;
      margin-right: 40px;
      @include response(1200px) {
        margin-right: -30px;
        margin-left: -30px;
      }
    }
    picture {
      display: block;
    }
    @include response(1200px) {
      margin-left: -30px;
      margin-right: -30px;
      margin-bottom: 50px;
    }
    &--mobile-margin {
      @include response(1200px) {
        margin-bottom: -90px;
      }
    }
  }
}
