.btn {
  font-family: "Poppins", sans-serif;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  font-weight: 600;
  font-size: 15px;
  line-height: 26px;
  color: $blue;
  text-transform: uppercase;
  background: #c5c1ff;
  border-radius: 40px;
  padding: 12px 25px;
  border: 0;
  display: inline-flex;
  vertical-align: top;
  align-items: center;
  justify-content: center;
  @include desktop-hover() {
    background: $blue;
    color: #fff;
    svg {
      fill: #fff;
    }
  }
  svg {
    fill: $blue;
    transition: 0.3s ease-in-out;
    flex: 0 0 auto;
    margin-left: 10px;
  }
}

.btn-2 {
  font-family: "Poppins", sans-serif;
  position: relative;
  display: inline-flex;
  vertical-align: top;
  align-items: center;
  text-decoration: none;
  font-weight: 600;
  font-size: 15px;
  line-height: 1;
  color: $blue;
  @include response(374px) {
    font-size: 13px;
  }
  @include desktop-hover() {
    span {
      &:nth-child(2) {
        transform: translateX(3px);
      }
    }
    svg {
      stroke: $blue;
    }
  }
  span {
    position: relative;
    transition: all 0.3s ease-in-out;
    &:nth-child(1) {
      z-index: 1;
      margin-right: -15px;
      @include response(374px) {
        margin-right: -10px;
      }
    }
    &:nth-child(2) {
      flex: 0 0 64px;
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      @include response(374px) {
        flex: 0 0 50px;
        width: 50px;
        height: 50px;
      }
    }
  }
  svg {
    stroke: #c5c1ff;
    transition: 0.3s ease-in-out;
  }
  &--bg {
    span {
      &:nth-child(2) {
        background: #f9f9ff;
      }
    }
  }
  &--revers {
    flex-direction: row-reverse;
    color: $black;
    @include desktop-hover() {
      span {
        &:nth-child(2) {
          transform: translateX(-3px);
        }
      }
    }
    span {
      &:nth-child(1) {
        margin-right: 0;
        margin-left: -15px;
        @include response(374px) {
          margin-left: -10px;
        }
      }
    }
    svg {
      transform: scaleX(-1);
    }
  }
  &--circle {
    @include desktop-hover() {
      span {
        &:nth-child(2) {
          &::before {
            background: $blue;
          }
        }
      }
    }
    span {
      &:nth-child(2) {
        &::before {
          content: "";
          position: absolute;
          width: 12px;
          height: 12px;
          top: 50%;
          left: 50%;
          margin-top: -6px;
          margin-left: -6px;
          background: #c5c1ff;
          transition: 0.3s ease-in-out;
          border-radius: 50%;
        }
      }
    }
  }
  &--color {
    color: #c5c1ff;
    @include desktop-hover() {
      color: #fff;
      span {
        &:nth-child(2) {
          &::before {
            background: #fff;
          }
        }
      }
    }
    span {
      &:nth-child(2) {
        background: rgba(197, 193, 255, 0.05);
      }
    }
  }
}
