.blog {
  position: relative;
  padding-top: 100px;
  padding-bottom: 150px;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__box {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    overflow: initial;
    margin-bottom: 150px;
    @include response(1200px) {
      margin-bottom: 0;
      display: block;
    }
  }
  &__holder {
    position: relative;
    flex: 0 0 850px;
    max-width: 850px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 50px;
    }
  }
  &__list {
    position: relative;
    @include response(1200px) {
      margin-bottom: 40px;
    }
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 75px;
    @include response(1200px) {
      margin-bottom: 50px;
    }
    @include response(767px) {
      display: block;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__item-img {
    position: relative;
    flex: 0 0 430px;
    max-width: 430px;
    width: 100%;
    margin-right: 51px;
    overflow: hidden;
    .blog__item--small & {
      flex: 0 0 300px;
      max-width: 300px;
      @include response(767px) {
        max-width: 100%;
      }
    }
    @include response(1200px) {
      flex: 0 0 40%;
      max-width: 40%;
    }
    @include response(767px) {
      display: block;
      margin-bottom: 15px;
      max-width: 100%;
      margin-right: 0;
    }
    @include desktop-hover() {
      img {
        transform: scale(1.05);
      }
    }
    img {
      width: 100%;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-box {
    position: relative;
  }
  &__item-title {
    display: block;
    font-family: "Raleway";
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    color: #011a28;
    text-decoration: none;
    margin-bottom: 25px;
    transition: 0.3s ease-in-out;
    .blog__item--small & {
      font-size: 28px;
      @include response(767px) {
        font-size: 27px;
      }
    }
    @include desktop-hover() {
      color: $blue;
    }
  }
  &__item-bottom {
    position: relative;
    display: flex;
    align-items: center;
  }
  &__item-date {
    position: relative;
    flex: 0 0 auto;
    margin-right: 25px;
    font-size: 15px;
    line-height: 175%;
    color: #011a28;
  }
  &__item-btn {
    position: relative;
    flex: 0 0 auto;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      transform: translateX(4px);
    }
    svg {
      stroke: $blue;
    }
  }
  &__aside {
    position: sticky;
    top: 94px;
    flex: 0 0 260px;
    max-width: 260px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      top: auto;
    }
  }
  &__aside-title {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: #011a28;
    margin-bottom: 25px;
    @include response(1200px) {
      margin-bottom: 10px;
    }
  }
  &__aside-list {
    position: relative;
    padding: 0;
    margin: 0 0 25px;
    list-style: none;
    @include response(1200px) {
      display: flex;
      margin-left: -30px;
      margin-right: -30px;
      padding-left: 30px;
      padding-right: 30px;
      overflow-x: auto;
    }
  }
  &__aside-list-item {
    position: relative;
    margin-bottom: 10px;
    @include response(1200px) {
      flex: 0 0 auto;
      margin-right: 10px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__aside-list-link {
    display: flex;
    align-items: center;
    text-decoration: none;
    border: 1px solid #cacad7;
    border-radius: 5px;
    padding: 5px 15px;
    font-size: 13px;
    line-height: 175%;
    color: #7d7d8c;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $blue;
    }
  }
  &__aside-list-img {
    position: relative;
    flex: 0 0 18px;
    max-width: 18px;
    width: 18px;
    margin-right: 10px;
    display: flex;
  }
  &__proposal {
    position: relative;
    border-radius: 5px;
    background: $blue;
    padding: 25px 30px 40px;
    text-align: center;
  }
  &__proposal-label {
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
    color: #c5c1ff;
    margin-bottom: 25px;
    text-align: left;
  }
  &__proposal-logo {
    margin-bottom: 23px;
    text-align: left;
  }
  &__proposal-img {
    margin-bottom: 14px;
    margin-left: -15px;
    margin-right: -15px;
  }
  &__proposal-btn {
    position: relative;
    &.btn {
      @include desktop-hover() {
        background: $black;
      }
    }
  }
  &__load {
    text-align: center;
    position: relative;
  }
  &__load-btn {
    @include desktop-hover() {
      svg {
        transform: rotate(180deg);
      }
    }
  }
}
