.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  @include response(1200px) {
    transition: 0.3s ease-in-out;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f9f9ff;
    transition: 0.3s ease-in-out;
    transform: translateY(-100%);
    box-shadow: 0px 5px 75px rgba(1, 26, 40, 0.06);
  }
  &--scroll {
    &::before {
      transform: translateY(0);
    }
  }
  &__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 50px;
    padding-bottom: 10px;
    transition: 0.3s ease-in-out;
    .header--scroll & {
      padding-top: 10px;
    }
    @include response(1200px) {
      margin-left: -10px;
      margin-right: -10px;
      padding-top: 20px;
    }
  }
  &__left {
    position: relative;
    display: flex;
    align-items: center;
    flex: 0 0 auto;
  }
  &__logo {
    position: relative;
    flex: 0 0 auto;
    picture {
      display: block;
    }
    @include response(1200px) {
      flex: 0 0 90px;
      max-width: 90px;
      width: 100%;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    z-index: 1;
  }
  &__btn {
    flex: 0 0 auto;
    margin-right: 25px;
    .active-menu & {
      background: #fff;
      color: $blue;
      @include desktop-hover() {
        background: #c5c1ff;
      }
    }
    @include response(1200px) {
      margin-right: 0;
    }
  }
  &__menu-btn {
    position: relative;
    flex: 0 0 50px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: $blue;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    .active-menu & {
      background: $black;
      @include desktop-hover() {
        background: #ec4b37;
      }
    }
    @include desktop-hover() {
      background: $black;
    }
  }
  &__menu-btn-open {
    transition: 0.3s ease-in-out;
    .active-menu & {
      transform: scale(0);
    }
  }
  &__menu-btn-close {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-top: -9px;
    margin-left: -9px;
    transition: 0.3s ease-in-out;
    transform: scale(0);
    .active-menu & {
      transform: scale(1);
    }
  }
  &__menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: $blue;
    visibility: hidden;
    transform: translateY(-100%);
    transition: 0.3s ease-in-out;
    overflow: hidden;
    .active-menu & {
      visibility: visible;
      transform: translateY(0);
      &::before {
        transform: scale(1);
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      margin-top: -50vw;
      left: -36vw;
      width: 100vw;
      height: 100vw;
      border-radius: 50%;
      background: $black;
      @include response(1200px) {
        width: 575px;
        height: 575px;
        left: -373px;
        top: -425px;
        margin-top: 0;
      }
    }
  }
  &__menu-box {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    max-width: 1564px;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    margin-left: auto;
    margin-right: auto;
    @include response(767px) {
      display: block;
    }
  }
  &__menu-left {
    position: relative;
    flex: 0 0 775px;
    max-width: 775px;
    width: 100%;
    @include response(1200px) {
      display: none;
    }
  }
  &__menu-right {
    position: relative;
    flex: 0 0 400px;
    max-width: 400px;
    width: 100%;
    @include response(1200px) {
      max-width: 100%;
      height: 100%;
      padding-top: 110px;
      padding-bottom: 60px;
      overflow-y: auto;
    }
  }
  &__menu-get-box {
    display: none;
    @include response(1200px) {
      display: block;
      text-align: center;
      padding-top: 8px;
    }
  }
  &__nav {
    position: relative;
    @include response(1200px) {
      margin-bottom: 28px;
    }
  }
  &__nav-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 67px;
      text-align: right;
      @include response(1200px) {
        margin-bottom: 20px;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
      font-family: "Raleway";
      font-style: normal;
      font-weight: 600;
      font-size: 36px;
      line-height: 50px;
      text-align: right;
      color: #fff;
      position: relative;
      overflow: hidden;
      transition: 0.3s ease-in-out;
      perspective: 1px;
      padding-left: 10px;
      padding-right: 10px;
      margin-right: -10px;
      @include response(1200px) {
        font-size: 23px;
        line-height: 40px;
        display: block;
        text-align: center;
        margin-right: 0;
      }
      @include desktop-hover() {
        color: $black;
        &::before {
          transform: translateX(0);
        }
      }
      &::before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: #fff;
        transform: translateX(110%);
        transition: 0.3s ease-in-out;
        z-index: -1;
      }
    }
  }
  &__menu-row {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 93px;
  }
  &__menu-logo {
    position: relative;
    flex: 0 0 auto;
    @include response(1200px) {
      max-width: 90px;
      position: absolute;
      top: 29px;
      left: 20px;
    }
  }
  &__menu-text {
    position: relative;
    max-width: 300px;
    width: 100%;
    font-size: 18px;
    line-height: 175%;
    color: #fff;
  }
  &__menu-contacts {
    position: relative;
    margin-bottom: 80px;
    display: flex;
    flex-wrap: wrap;
  }
  &__menu-contacts-item {
    position: relative;
    flex: 0 0 auto;
    margin-bottom: 40px;
    margin-right: 60px;
    display: flex;
  }
  &__menu-contacts-title {
    font-weight: 600;
    font-size: 23px;
    line-height: 175%;
    color: #c5c1ff;
    flex: 0 0 auto;
    margin-right: 8px;
  }
  &__menu-contacts-box {
    position: relative;
  }
  &__menu-contacts-link {
    display: block;
    text-decoration: none;
    font-weight: 600;
    font-size: 23px;
    line-height: 175%;
    color: #fff;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: #c5c1ff;
    }
  }
  &__menu-contacts-address {
    font-style: normal;
    font-size: 15px;
    line-height: 175%;
    color: #fff;
    padding-top: 8px;
  }
}
