.top-box {
  position: relative;
  background: #f9f9ff;
  padding-top: 287px;
  padding-bottom: 220px;
  min-height: 700px;
  @include response(1440px) {
    padding-top: 190px;
    padding-bottom: 180px;
    min-height: 500px;
  }
  @include response(1200px) {
    padding-top: 100px;
    padding-bottom: 100px;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &--small {
    min-height: auto;
    padding-bottom: 118px;
  }
  &__picture {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    img {
      height: 100%;
      width: auto;
    }
  }
  &__holder {
    padding-left: 152px;
    padding-right: 152px;
    @include response(1550px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__title {
    position: relative;
  }
  &__description {
  }
}
