.text-content {
  position: relative;
  font-size: 15px;
  line-height: 175%;
  color: #011a28;
  p {
    margin: 10px 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  ol {
    list-style: none;
    padding: 0;
    counter-reset: myCounter;
    margin: 10px 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 15px;
      position: relative;
      padding-left: 35px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) " _";
        position: absolute;
        left: 0;
        top: 0;
        color: $blue;
        font-weight: 600;
      }
    }
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 10px;
      position: relative;
      padding-left: 35px;
      &::before {
        content: "";
        position: absolute;
        left: 4px;
        top: 12px;
        width: 11px;
        height: 2px;
        background: $blue;
        opacity: 0.2;
      }
      &::after {
        content: "";
        position: absolute;
        left: 8px;
        top: 9px;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid $blue;
        border-right: 2px solid $blue;
        transform: rotate(-45deg);
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 0 0 40px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    font-family: "Raleway";
    font-weight: 600;
    font-size: 36px;
    line-height: 135%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    font-family: "Raleway";
    font-weight: 600;
    font-size: 31px;
    line-height: 135%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    font-family: "Raleway";
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-weight: 600;
    font-size: 23px;
    line-height: 175%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  img {
    display: inline-block;
    vertical-align: top;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: $blue;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  table {
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    border-collapse: collapse;
    &:last-child {
      margin-bottom: 0;
    }
    thead {
      tr {
        td {
          font-weight: 500;
          font-size: 18px;
          padding: 27px 10px;
          text-align: center;
          &:first-child {
            background: #f9f9ff;
          }
        }
      }
    }
    tr {
    }
    td {
      width: 33.333%;
      border: 1px solid #cacad7;
      padding: 10px 15px;
      line-height: 20px;
      height: 61px;
      &:first-child {
        border-color: #7d7d8c;
      }
      > span {
        display: flex;
        align-items: center;
        svg {
          flex: 0 0 auto;
          margin-right: 15px;
        }
      }
    }
  }
  .text-cell {
    display: flex;
    margin-left: -40px;
    flex-wrap: wrap;
    @include response(767px) {
      display: block;
      margin-left: 0;
    }
    > div {
      flex: 0 0 calc(50% - 40px);
      max-width: calc(50% - 40px);
      width: 100%;
      margin-left: 40px;
      margin-bottom: 40px;
      @include response(767px) {
        max-width: 100%;
        margin-left: 0;
        margin-bottom: 30px;
      }
    }
  }
  ul.tags {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    li {
      padding: 0;
      font-weight: 500;
      font-size: 13px;
      line-height: 175%;
      flex: 0 0 auto;
      margin-left: 5px;
      margin-right: 5px;
      margin-bottom: 10px;
      &::before {
        display: none;
      }
      &::after {
        display: none;
      }
    }
    a {
      background: #f9f9ff;
      border-radius: 5px;
      padding: 5px 15px;
      display: flex;
      align-items: center;
      text-decoration: none;
      color: $black;
      transition: 0.3s ease-in-out;
      @include desktop-hover() {
        background: #e7e7ef;
      }
      img {
        flex: 0 0 auto;
        margin: 0 10px 0 0;
      }
    }
  }
  .color-box {
    background: #c5c1ff;
    padding: 30px;
  }
  .color-box-icon {
    padding: 30px;
    background: rgba(236, 75, 55, 0.1);
    display: flex;
    align-items: flex-start;
    @include response(767px) {
      display: block;
    }
    img {
      flex: 0 0 32px;
      margin-right: 23px;
    }
  }
}
