.footer {
  position: relative;
  flex: 0 0 auto;
  padding-top: 150px;
  padding-bottom: 150px;
  border-top: 1px solid #cacad7;
  @include response(1200px) {
    padding-top: 75px;
    padding-bottom: 75px;
  }
  &__top {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin-bottom: 150px;
    @include response(1200px) {
      margin-bottom: 20px;
      flex-wrap: wrap;
    }
  }
  &__cell {
    position: relative;
    flex: 0 0 25%;
    max-width: 25%;
    width: 100%;
    padding-right: 20px;
    &:last-child {
      padding-right: 0;
    }
    @include response(1200px) {
      margin-bottom: 40px;
      flex: 0 0 33.333%;
      max-width: 33.333%;
      &:nth-child(1) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
    @include response(767px) {
      flex: 0 0 50%;
      max-width: 50%;
      &:nth-child(3) {
        padding-right: 0;
        padding-left: 20px;
      }
      &:nth-child(4) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }
  &__logo {
    position: relative;
    margin-bottom: 55px;
    @include response(1200px) {
      margin-bottom: 29px;
    }
  }
  &__logo-link {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  &__text {
    max-width: 180px;
    font-size: 14px;
    line-height: 190%;
    color: #011a28;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__cell-title {
    font-weight: 600;
    font-size: 12px;
    line-height: 175%;
    color: #7d7d8c;
    margin-bottom: 25px;
  }
  &__nav {
    position: relative;
  }
  &__nav-list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: block;
      text-decoration: none;
      font-weight: 500;
      font-size: 15px;
      line-height: 175%;
      color: $black;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
  &__btn {
    display: inline-block;
    vertical-align: top;
    text-decoration: none;
    cursor: pointer;
    font-weight: 500;
    font-size: 15px;
    line-height: 175%;
    color: $blue;
    margin-bottom: 15px;
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__contacts {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 15px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: block;
      text-decoration: none;
      font-size: 15px;
      line-height: 175%;
      color: $black;
      @include desktop-hover() {
        text-decoration: underline;
      }
    }
  }
  &__bottom {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(1200px) {
      display: block;
    }
  }
  &__left {
    position: relative;
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    max-width: 50%;
    @include response(1200px) {
      max-width: 100%;
      margin-bottom: 20px;
    }
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    max-width: 50%;
    @include response(1200px) {
      max-width: 100%;
    }
  }
  &__copy {
    font-size: 13px;
    line-height: 175%;
    color: #7d7d8c;
  }
}
