.author {
  position: relative;
  border-top: 1px solid #cacad7;
  border-bottom: 1px solid #cacad7;
  padding-top: 75px;
  padding-bottom: 75px;
  &__box {
    display: flex;
    align-items: flex-start;
    max-width: 640px;
    margin-left: auto;
    margin-right: auto;
    @include response(767px) {
      display: block;
    }
  }
  &__ava {
    flex: 0 0 100px;
    max-width: 100px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    background: #f9f9ff;
    margin-right: 40px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__holder {
    position: relative;
    padding-top: 17px;
    max-width: 460px;
    max-width: 100%;
  }
  &__title {
    font-family: "Raleway";
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    text-transform: uppercase;
    color: $blue;
  }
  &__name {
    margin-bottom: 30px;
    font-weight: 600;
    font-size: 23px;
    line-height: 175%;
    @include response(767px) {
      margin-bottom: 15px;
    }
  }
  &__text {
    position: relative;
  }
}
